

  
  
  .navigationMenu {
    // margin-left: 200px;
    width: 100%;
    display: flex;
    justify-content: center;

  }
  nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    position: relative;
    display: flex;
    justify-content: center;
    gap: 100px;
    }
    
  nav ul li {
    display:inline-block;
    background-color: #ffffff;
    }
  
  nav a {
    display:block;
    padding:0 10px; 
    color:#000000;
    // font-size:17px;
    line-height: 50px;
    text-decoration:none;
    font-weight:300 ;
    font-family:Arial, Helvetica, sans-serif;
  }
    
  // nav ul li:hover > ul {
  //   display:inherit;
  // }
    
  nav ul ul li {
    width:170px;
    float:none;
    display:list-item;
    position: relative;
  }
  
  nav ul ul ul li {
    position: relative;
    top:-60px; 
    left:170px;
  }
  
  
  // li > a:after { content:  ' '; }
  li > a:only-child:after { content: ''; }
  
.is-sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  // box-shadow: 0 2px 24px 0 rgb(0 0 0 / 15%);
  background-color: #ffffff !important;
  border: 0;
}

.header-section {
  display: flex;
  width: 100%;
  // position: relative;
}
.borderLeft {
  border-bottom: 2px solid #000000;
  position: absolute;
  bottom: 90%;
}


.Logo {
  display: none;
}
.navbar {
  display: none;
}


@media only screen and (max-width: 943px){

  .navbar {
    display: block;
    width: 100vw;
  }
  
  .nav-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
  }
  
  .navbar .menu-items {
    display: flex;
  }
  
  .navbar .nav-container li {
    list-style: none;
  }

  .menu-items {
    display: block;
  }
  
  .navbar .nav-container a {
    text-decoration: none;
    color: #0e2431;
    font-weight: 500;
    font-size: 1.2rem;
    padding: 0.7rem;
  }
  
  .navbar .nav-container a:hover{
      font-weight: bolder;
  }
  
  .nav-container {
    display: block;
    position: relative;
    height: 60px;
  }
  
  .nav-container .checkbox {
    position: absolute;
    display: block;
    height: 32px;
    width: 32px;
    top: 20px;
    left: 20px;
    z-index: 5;
    opacity: 0;
    cursor: pointer;
  }
  
  .nav-container .hamburger-lines {
    display: block;
    height: 26px;
    width: 32px;
    position: absolute;
    top: 17px;
    left: 20px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .nav-container .hamburger-lines .line {
    display: block;
    height: 4px;
    width: 100%;
    border-radius: 10px;
    background: #0e2431;
  }
  
  .nav-container .hamburger-lines .line1 {
    transform-origin: 0% 0%;
    transition: transform 0.4s ease-in-out;
  }
  
  .nav-container .hamburger-lines .line2 {
    transition: transform 0.2s ease-in-out;
  }
  
  .nav-container .hamburger-lines .line3 {
    transform-origin: 0% 100%;
    transition: transform 0.4s ease-in-out;
  }
  
  .navbar .menu-items {
    padding-top: 120px;
    // box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
    background: #ffffff;
    height: 100vh;
    width: 100vw;
    transform: translate(-150%);
    display: flex;
    flex-direction: column;
    margin-left: -75px;
    padding-left: 50px;
    transition: transform 0.5s ease-in-out;
    text-align: center;
    margin-right: -150px;
  }
  
  .navbar .menu-items li {
    margin-bottom: 1.2rem;
    font-size: 1.5rem;
    font-weight: 500;
  }
  
  .logo {
    position: absolute;
    top: -15px;
    right: 15px;
    font-size: 1.2rem;
    color: #0e2431;

    & .Logo{
      display: block;
      width: 160px;
      height: 80px;
    }
  }
  
  .nav-container input[type="checkbox"]:checked ~ .menu-items {
    transform: translateX(0);
  }
  
  .nav-container input[type="checkbox"]:checked ~ .hamburger-lines .line1 {
    transform: rotate(45deg);
  }
  
  .nav-container input[type="checkbox"]:checked ~ .hamburger-lines .line2 {
    transform: scaleY(0);
  }
  
  .nav-container input[type="checkbox"]:checked ~ .hamburger-lines .line3 {
    transform: rotate(-45deg);
  }
  
  .nav-container input[type="checkbox"]:checked ~ .logo{
    display: none;
  }
  
  .navigationMenu ul li {
    display: none; 
    background-color: #ffffff;
    }
}